import React, { Component } from 'react';
import './ContactPreview/css/ContactPreview.css'

export default class ContactPreview extends Component {
    onSelectContact = () => {
        if (this.props.onSelectContact)
            this.props.onSelectContact(this.props.contact);
    }
    render() {
        const { props } = this;
        return <li className="contact-list-item flex row" onClick={this.onSelectContact}>
            <img className="contact-img" src={props.contact.imgUrl} />
            <h2 className="contact-name">{props.contact.name}</h2>
            {/* <h4 className="">{props.contact.email}</h4>
            <h4 className="">{props.contact.phone}</h4> */}
        </li>
    }
}