import React, { Component } from 'react';
import { connect } from 'react-redux';
// import { Link } from 'react-router-dom';
import '../../pages/Home/css/Home.css'
import bitcoinSmall from '../../assets/img/bitcoin-small.png';

import { loadUsers } from '../../actions/UserActions.js';

class Home extends Component {
  state = {

  };
  componentDidMount() {
    this.props.loadUsers();
  }

  // handleChange = ev => {
  //   const { name, value } = ev.target;
  //   this.setState(prevState => ({
  //     reviewToEdit: {
  //       ...prevState.reviewToEdit,
  //       [name]: value
  //     }
  //   }));
  // };

  toContacts = () => {
    this.props.history.push('/contacts/');
  }

  render() {
    const { users, loggedInUser } = this.props
    return (
      <div className="home flex">
        {users && loggedInUser && <div className="user-info-container flex row">
          <div className=" flex col">
            <span className="user-name-field">Hello {loggedInUser.username}</span>
            <div className="user-amount-container flex row">
              <span className="user-amount-field">{loggedInUser.amount}</span>
              <img className="bitcoin-small-img" src={bitcoinSmall}></img>
            </div>
          </div>
          <button className="contacts-btn" onClick={this.toContacts}>Contacts</button>
        </div>}



        {/* {this.state.selectedContact ?
          <ContactDetails contact={this.state.selectedContact} onBackClick={this.removeSelected} onDelete={this.onDelete}></ContactDetails>
          : [<Filter onFilter={this.onFilter}></Filter>
            , <ContactList contacts={this.state.contacts} onSelectContact={this.onSelectContact}></ContactList>]} */}

      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    users: state.user.users,
    loggedInUser: state.user.loggedInUser
  };
};
const mapDispatchToProps = {
  loadUsers
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
