import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/ContactPage.css'

// Actions
import { loadUsers } from '../../actions/UserActions.js';
import { loadContacts } from '../../actions/ContactActions.js';


// import Filter from '../../cmps/Filter.jsx'
import ContactList from '../../cmps/ContactList/ContactList.jsx'

class ContactPage extends Component {
    state = {
        selectedContact: null
    };
    componentDidMount() {
        this.props.loadContacts();
        this.props.loadUsers();
    }

    onSelectContact = (contact) => {
        this.setState({ selectedContact: contact })
    }

    render() {
        const { loggedInUser, users, contacts } = this.props
        // const { users, contacts } = this.state
        return (
            <section className="contact-page-container">
                <h1>Contacts</h1>
                {/* <ContactForm addContact={this.onAddContact}></ContactForm> */}
                {users && loggedInUser &&
                    // <Filter onFilter={this.onFilter}></Filter> ,
                    <ContactList contacts={contacts} onSelectContact={this.onSelectContact}></ContactList>}
            </section>
        );
    }
}

const mapStateToProps = state => {
    return {
        users: state.user.users,
        loggedInUser: state.user.loggedInUser,
        contacts: state.contact.contacts
    };
};
const mapDispatchToProps = {
    loadUsers,
    loadContacts
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactPage);
