import React, { Component } from 'react'
import ContactPreview from "../ContactPreview.jsx";
import "./css/ContactList.css";

export default class ContactList extends Component {
    constructor(props) {
        super(props)
        console.log('this.props: ' + this.props.contacts)
    }

    render() {
        const { contacts, onSelectContact } = this.props
        return (
            <div className="contact-list-container flex">
                <ul className="contact-list">{contacts.map((contact) =>
                    <ContactPreview key={contact._id} contact={contact} onSelectContact={onSelectContact}></ContactPreview>)}
                </ul>
            </div>
        )
    }
}

